@import url(https://fonts.googleapis.com/css?family=Mali);
body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Mali", sans-serif;
  font-size: 18px;
  background: turquoise;
}
.Desktop {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.List {
    width: 90%;
    max-width: 500px;
    min-width: 300px;
    height: 65vh;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #f9f9f9;
    border: 1px solid black;
    border-radius: 10px 10px 10px 10px;
    box-shadow: 1px 1px 1px 1px slategray;
    position: relative;
    top: 10%;
}

.list-header {
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: 1.1px;
    line-height: 1.8;
}

.list-form {
    width: 100%;
    background: #343A3F;
}

.alert-message {
    margin-left: 20px;
    display: flex;
    align-items: center;
    color: red;
    font-family: Arial, Helvetica, sans-serif;
}

.add-form{
    width: 100%;
    display: flex;
    padding: 5px 0;
}

.edit-form{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 0 10px 0;
}

input {
    margin-left: 20px;
    border: 1px solid slategray;
    font-size: 0.9rem;
}

.popup-alert {
    padding: 10px;
    position: absolute;
    background: white;
    border: 1px solid black;
    text-align: center;
}

.alert-btn {
    margin-bottom: 5px;
    padding: 5px;
    background: lightgray;
    color: black;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: bold;
    border: 1px solid black;
    border-radius: 3px;
}
.list-items {
    width: 90%;
    height: 70%;
    overflow: auto;
}

.ListItem {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px dotted slategray;
    line-height: 2;
}

.ListItem > span {
    width: 70px;
    opacity: 0;
}

.ListItem:hover > span {
    opacity: 1;
}

textarea {
    margin-left: 20px;
    width: 70%;
    background: transparent;
    border: none;
    font-family: "Mali", sans-serif;
    font-size: 0.9em;
    letter-spacing: 1.1px;
    line-height: 1;
}

button {
    font-family: "Mali", sans-serif;
    font-size: 0.9em;
    background: transparent;
    border: none;
}

button:focus {
    outline: none;
}

.status-btn {
    text-align: left;
    width: 80%;
}

.status-font {
    color: black;
}

.status, .edit, .remove, .save, .cancel {
    color: darkcyan;
}

.add {
    color: greenyellow;
    position: relative;
    top: 2px;
}

.edit:hover, .save:hover {
    color: green;
}

.cancel:hover, .remove:hover {
    color: red;
}
.background-items {
    width: 85%;
    min-width: 300px;
    max-width: 600px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    top: 20px;
    z-index: 1;
}
  
.phone {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 2px;
    width: 90px;
    height: 160px;
    min-width: 90px;
    min-height: 160px;
    background: gainsboro;
    border-radius: 7px 7px 7px 7px;
    box-shadow: 1px 1px 2px 1px darkslategray;
    transform: rotate(340deg);
}

.phone-camera1 {
    width: 23px;
    height: 4px;
    background: black;
    border-radius: 10px 0px 0px 0px;
}

.phone-camera2 {
    width: 23px;
    height: 4px;
    background: black;
    border-radius: 0px 10px 0px 0px;
}

.phone-screen {
    width: 93%;
    height: 95%;
    border-radius: 0 0 5px 5px;
    background: black;
}

.passport {
    transform: rotate(20deg);
}

.passport-book {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    width: 110px;
    height: 150px;
    min-width: 110px;
    min-height: 150px;
    color: goldenrod;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
    background: midnightblue;
    border-radius: 3px 3px 3px 3px;
    box-shadow: 1px 1px 2px 1px darkslategrey;
}

.passport-globe {
    font-size: 3em;
}
